"use client"
import React from 'react'
import Lottie from 'lottie-react'
import mateAnimation from '../assets/mainscene.json'
import { NodeIcon } from '../icons/TechIcons'
import '../styles/components/background-title.css'

const BackgroundTitle = () => {
  return (
    <div className="background-title-container">
      {/* Node.js fijo detrás del mate - Posición absoluta */}
      <div className="fixed-nodejs-behind-mate">
        <div className="nodejs-icon-wrapper">
          <NodeIcon className="nodejs-icon" />
          <div className="lottie-overlay">
            <Lottie 
              animationData={mateAnimation} 
              loop={true}
              className="mate-lottie"
            />
          </div>
        </div>
      </div>
    </div>
  )
}

export default BackgroundTitle
