// Función helper para obtener el icono correcto según la tecnología
export const getTechIcon = (tech) => {
  const iconMap = {
    "React": <ReactIcon />,
    "Node.js": <NodeIcon />,
    "MongoDB": <MongoIcon />,
    "Express": <ExpressIcon />,
    "JavaScript": <JavaScriptIcon />,
    "Tailwind CSS": <TailwindIcon />,
    "Framer Motion": <FramerMotionIcon />,
    "CSS3": <CSS3Icon />,
    "HTML5": <HTML5Icon />,
    "Firebase": <FirebaseIcon />,
    "Java": <JavaIcon />,
    "Spring Boot": <SpringIcon />,
    "MySQL": <MySQLIcon />,
    "Android": <AndroidIcon />,
    "Bootstrap": <BootstrapIcon />,
    "Responsive Design": <CSS3Icon />,
    "Python": <PythonIcon />,
    "FastAPI": <FastAPIIcon />,
    "Thymeleaf": <ThymeleafIcon />,
    "PostgreSQL": <PostgreSQLIcon />,
    "HTML/CSS": <HTML5Icon />,
    "JPA": <DatabaseIcon />,
    "Maven": <JavaIcon />,
    "SQL": <DatabaseIcon />,
    "SCSS": <CSS3Icon />,
    "Axios": <JavaScriptIcon />,
    "JWT Authentication": <DatabaseIcon />,
    // Herramientas
    "Git": <GitHubIcon />,
    "GitHub": <GitHubIcon />,
    "IntelliJ IDEA": <DatabaseIcon />,
    "Eclipse": <DatabaseIcon />,
    "Postman": <ExternalLinkIcon />,
    "Docker": <DatabaseIcon />,
    "Figma": <DatabaseIcon />,
    "Claude": <DatabaseIcon />,
    "Gemini": <DatabaseIcon />,
    "N8N": <DatabaseIcon />,
    // Metodologías
    "Scrum": <DatabaseIcon />,
    "Agile": <DatabaseIcon />,
    "Sprints": <DatabaseIcon />,
    "Teamwork": <DatabaseIcon />,
    "UX/UI Design": <DatabaseIcon />,
    "User Research": <DatabaseIcon />
  }

  return iconMap[tech] || <DatabaseIcon />
}